<template>
  <div class="container-fluid">
    <div class="row mt-5">
      <div class="col">
        <p class="col-12 title-section">Estadisticas de Envío de Emails</p>
        <div class="col-12" style="display: inline-flex">
          <select
            v-model="typeQuery"
            class="form-control mr-4 select-typequery"
            id="exampleFormControlSelect1"
          >
            <option>Diario</option>
            <option>Mensual</option>
          </select>
          <button
            class="btn btn-dark btn-secondary"
            @click="consultar()"
            style="background: #337bbe"
          >
            <span
              v-if="this.isLoading"
              class="spinner-border spinner-border-sm"
              role="status"
              aria-hidden="true"
            ></span>
            Consultar
          </button>
        </div>
      </div>
    </div>
    <div class="row mt-5">
      <div class="col-12">
        <div class="card card-barchart">
          <bar-chart class="bar-chart" ref="barChart"></bar-chart>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from "../store/index";
import moment from "moment";
import BarChart from "../components/BarChart";
import { getAbrevMonth, dame_color_aleatorio } from "../libs/funciones";
import config from "../config";
export default {
  name: "Home",
  components: { BarChart },
  data: () => {
    return {
      dateStart: "",
      dateEnd: "",
      typeQuery: "Diario",
      chartData: {},
      chartOptions: {},
    };
  },
  computed: {
    isLoading() {
      return this.$store.state.loadingData;
    },
  },
  created() {
    this.$store.commit("SET_LAYOUT", "principal-layout");
  },
  mounted() {
    this.dateStart = moment()
      .subtract(30, "d")
      .format("YYYY-MM-DD");
    this.dateEnd = moment().format("YYYY-MM-DD");
    this.consultar();
  },
  methods: {
    //Desde el botón se consulta el tipo de reporte
    async consultar() {
      this.$store.commit("SET_LOADING_DATA", true);
      switch (this.typeQuery) {
        case "Mensual":
          this.dateStart = moment()
            .subtract(12, "months")
            .format("YYYY-MM-DD");
          this.dateEnd = moment().format("YYYY-MM-DD");
          this.consultarMensual();
          break;
        default:
          this.dateStart = moment()
            .subtract(30, "d")
            .format("YYYY-MM-DD");
          this.dateEnd = moment().format("YYYY-MM-DD");
          this.consultarDiario();
          break;
      }
    },
    //Consultar API para informe diario
    async consultarDiario() {
      // Consultamos API
      var report = [];
      report = await store.dispatch("getReportDaily", {
        dateStart: this.dateStart,
        dateEnd: this.dateEnd,
      });
      //Formateamos la respuesta
      report = this.formatDaily(report);
      this.$store.commit("SET_LOADING_DATA", false);
      // Enviamos a graficar
      this.graficar(report, "daily");
      // this.renderBarChart();
    },
    //Consultar API para informe mensual
    async consultarMensual() {
      // Consultamos API
      var report = [];
      report = await store.dispatch("getReportMonthly", {
        dateStart: this.dateStart,
        dateEnd: this.dateEnd,
      });

      //Formateamos la respuesta
      report = this.formatMonthly(report);

      // Enviamos a graficar
      this.graficar(report, "monthly");
      this.$store.commit("SET_LOADING_DATA", false);
    },
    //Formatea la respuesta para mostrar informe por días
    formatDaily(report_info) {
      var tmpdateStart = this.dateStart;
      var dates = [];
      while (moment(tmpdateStart) < moment(this.dateEnd)) {
        tmpdateStart = moment(tmpdateStart)
          .add(1, "days")
          .format("YYYY-MM-DD");
        dates.push({ date: tmpdateStart, count: 0 });
      }
      const tmpDates = dates.map((item) => {
        const found = report_info.find((el) => el.date == item.date);
        if (found) {
          return {
            date: item.date,
            count: found.count,
          };
        }
        return {
          date: item.date,
          count: 0,
        };
      });
      return tmpDates;
    },
    // Formatea la respuesta para mostrar informe por mes
    formatMonthly(report_info) {
      var tmpDateStart = moment(this.dateStart).format("YYYY-MM-DD");
      var months = [];
      while (moment(tmpDateStart) < moment(this.dateEnd)) {
        tmpDateStart = moment(tmpDateStart)
          .add(1, "months")
          .format("YYYY-MM-DD");
        months.push({ month: tmpDateStart.substring(0, 7), count: 0 });
      }
      const tmpMonths = months.map((item) => {
        const found = report_info.find((el) => el.month == item.month);
        if (found) {
          return {
            month: getAbrevMonth(item.month) + "-" + item.month.substring(0, 4),
            count: found.count,
          };
        }
        return {
          month: getAbrevMonth(item.month) + "-" + item.month.substring(0, 4),
          count: 0,
        };
      });
      return tmpMonths;
    },
    // Grafica los datos
    graficar(report_info, type) {
      const report_labels = report_info.map((item) => {
        if (item.month) {
          return item.month;
        }
        if (item.date) {
          return item.date;
        }
      });
      const report_data = report_info.map((item) => {
        return item.count;
      });

      const colors = report_info.map((item) => {
        return dame_color_aleatorio();
      });

      var maxValue = Math.max(...report_data);

      this.chartData = {
        labels: report_labels,
        datasets: [
          {
            // label: "Enviados",
            data: report_data,
            backgroundColor: colors,
            // borderColor: "#1ab187",
            borderWidth: 1,
            barThickness: 25,
          },
        ],
      };
      this.chartOptions = {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false,
        },
        layout: {
          padding: {
            left: 20,
            right: 20,
            top: 50,
            bottom: 20,
          },
        },
        scales: {
          yAxes: [
            {
              ticks: {
                suggestedMin: 0,
                suggestedMax: maxValue * 1.1,
                // stepSize: maxValue / 10,
                beginAtZero: true,
              },
            },
          ],
        },
      };
      if (this.$refs.barChart) {
        this.$refs.barChart.render(this.chartData, this.chartOptions);
      }
    },
  },
};
</script>
<style>
.card-barchart {
  margin-bottom: 20px;
  margin-top: 0;
}
.bar-chart {
  width: 100%;
}
.title-section {
  font-size: 2rem;
  color: black;
  font-weight: 500;
}
.select-typequery {
  width: 20%;
}

@media only screen and (max-width: 1366px) {
  .select-typequery {
    width: 60%;
  }
}

@media only screen and (max-width: 1024px) {
  .select-typequery {
    width: 100%;
  }
}
</style>
